<template>
  <div class="area-main">
    <ControlsPanelBase
      :isVertical="true"
    >
      <template #controls-last>
        <div 
          @click="clickBackHandler"
          class="control tile-blue svg-back">
        </div>
      </template>
    </ControlsPanelBase>
    <div class="main">
      <div
        @click="clickGameHandler(game)"
        class="game-tile"
        :class="{selection: this.gameModes[game.id] && this.gameModes[game.id].selection}"
        v-for="(game) in gamesList"
        :key="game.id"
      >
        <div class="game-name">{{game.name}}</div>
        <div
          class="game-difficult"
          v-if="this.gameModes[game.id]"
        >
          Складність
        </div>
        <div class="game-description">{{game.description}}</div>
        <div
          class="game-mode-current"
          v-if="this.gameModes[game.id] && Object.keys(game.modes).length > 1"
          @click="$event.stopPropagation(); this.gameModes[game.id].selection = true"
        >
          {{ this.gameModes[game.id].mode }}
        </div>
        <div
          class="game-modes"
          v-if="this.gameModes[game.id] && Object.keys(game.modes).length > 1"
          @click="$event.stopPropagation()"
        >
        <div
            class="game-mode"
            v-for="(mode, modeId) in game.modes"
            :key="modeId"
            :class="{selected: this.gameModes[game.id].mode === modeId}"
            @click="setGameMode(game.id, modeId); this.gameModes[game.id].selection = false"
          >
            {{ modeId }}
          </div>
        </div>
      </div>
    </div>
    
  </div>
    
  
</template>

<script>
import Storage from '@/storage';
import GamesList from '@/config/gamesList';
import DomMixin from '@/mixins/domMixin';
import ControlsPanelBase from '@/components/ControlsPanelBase';
export default {
    mixins: [
      DomMixin,
    ],
    created() {
        this.domSetDocumentBackgroundUrl(require('/public/img/bg-empty1-1080p.png'));
        GamesList.forEach(game => {
        if (game.modes) {
          this.gameModes[game.id] = {
            mode: this.getGameMode(game.id, Object.keys(game.modes)[0]),
            selection: false,
          }
          // console.log('this.gameModes[game.id]', this.gameModes[game.id].mode)
        }
      });
    },
    components: {
      ControlsPanelBase
    },
    methods: {
      clickBackHandler() {
          this.$router.push({ name: 'home'});
      },
      clickGameHandler(game) {
        this.$router.push({ name: game.route, params: {gameId: game.id, modeId: this.getGameMode(game.id)} });
      },
      getGameMode(gameId, def = null) {
        return (this.gameModes[gameId] && this.gameModes[gameId].mode) ?? Storage.getItemValue('gamesModes', gameId, def);
      },
      setGameMode(gameId, modeId) {
        if (this.getGameMode(gameId) !== modeId) {
          this.gameModes[gameId].mode = modeId;
          Storage.setItemValue('gamesModes', gameId, modeId);
        }
      }
    },
    data() {
      return {
        gameModes: {},
        gamesList: GamesList,
      }
    }
}
</script>

